export class SliderPresenter {
    constructor(model, view) {
        this.model = model;
        this.view = view;
        this.init();
    }
    init() {
        const [min, max] = this.model.getRange();
        const value = this.model.getValues()[0];
        const percentage = ((value - min) / (max - min)) * 100;
        this.view.updateHandlePosition(percentage);
    }
}
