export class SliderModel {
    constructor({ min, max, step, values, }) {
        this.min = min;
        this.max = max;
        this.step = step;
        this.values = values;
    }
    getValues() {
        return this.values;
    }
    setValues(values) {
        this.values = values;
    }
    getStep() {
        return this.step;
    }
    getRange() {
        return [this.min, this.max];
    }
}
